*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  background-color: #0B0D17;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.container {
  position: relative;
  z-index: 0;
  margin: 0 auto;
  max-width: 1440px;
  width: 100%;
  height: auto;
  font-size: 18px;
  line-height: 32px;
  font-family: "Barlow", sans-serif;
  letter-spacing: 0;
  color: #FFFFFF;
  font-weight: 400;
  font-family: sans-serif;
}

.page-main {
  padding: 2.5rem;
}

.button {
  padding: 0.75rem;
  width: 127px;
  height: 46px;
  background-color: #FFFFFF;
  border-radius: 10px;
  cursor: pointer;
}
.button:hover {
  opacity: 0.8;
}

.page-header {
  padding: 1.75rem 2.5rem 0rem 8.375rem;
  max-width: 100%;
  width: 100%;
}

.main-nav {
  position: relative;
  gap: 0.8rem;
  display: flex;
  align-items: center;
  min-height: auto;
}
.main-nav__logo {
  margin-right: auto;
}
.main-nav__logo img {
  min-width: 36.82px;
  min-height: 36.97px;
}
@media (min-width: 1440px) {
  .main-nav__logo img {
    min-width: 36.82px;
    min-height: 36.97px;
  }
}
.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__wrapper {
  position: absolute;
  border-radius: 14px;
  padding: 1.25rem 2.5rem;
  right: 0;
  width: 100%;
}
.main-nav__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  column-gap: 2.1875rem;
  row-gap: 1.25rem;
}
.main-nav__contacts a {
  display: block;
  text-decoration: none;
  color: white;
  min-width: max-content;
  margin-right: 1rem;
}
.main-nav__toogle {
  display: block;
  position: absolute;
  width: 24px;
  height: 24px;
  top: 4px;
  right: 30px;
  z-index: 2;
  border: 0;
  cursor: pointer;
  color: black;
  background-color: transparent;
}
@media (min-width: 1440px) {
  .main-nav__toogle {
    display: none;
  }
}
.main-nav--opened .main-nav__toogle::before, .main-nav--opened .main-nav__toogle::after {
  content: "";
  position: absolute;
  top: 10px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: currentColor;
}
.main-nav--opened .main-nav__toogle::before {
  transform: translateY(-50%) rotate(45deg);
}
.main-nav--opened .main-nav__toogle::after {
  transform: translateY(-50%) rotate(-45deg);
}
.main-nav--closed .main-nav__toogle::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: black;
  box-shadow: 0 8px 0 0 #000, 0 16px 0 0 #000;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    display: block;
    position: relative;
  }
  .main-nav--nojs .main-nav__toogle {
    display: none;
  }
}

.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  border-radius: 15px;
  padding-right: 4.0625rem;
  min-width: 30%;
}
.site-list__item a {
  text-decoration: none;
  color: inherit;
}
.site-list__item a:hover {
  opacity: 0.6;
}